import React, { useState } from "react";
import axios from "axios";
import swal from "sweetalert";
import FormBase from "../components/FormBase/FormBase";
import FormHeader from "../components/FormHeader/FormHeader";
import FormInput from "../components/FormInput/FormInput";
import FormSubmit from "../components/FormSubmit/FormSubmit";
import FormLoader from "../components/FormLoader/FormLoader";
import { API } from "../../config";
import classNames from "./Reset.module.css";

const Reset = ({ history }) => {
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [loading, setLoading] = useState(false);

  const resetPassword = () => {
    const urlParams = new URLSearchParams(window.location.search);
    setLoading(true);
    axios
      .patch(`${API}/reset-password/${urlParams.get("token")}`, {
        password: password,
        confirmPassword: confirmPassword,
      })
      .then((response) => {
        setLoading(false);
        console.log(response);
        swal(
          "Reset success!",
          "You will now redirect to the App page!",
          "success",
          {
            button: "Okay",
          }
        ).then(() => {
          history.push("/app");
        });
      })
      .catch((err) => {
        setLoading(false);
        swal("Error!", `${err.response.data.message}`, "error", {
          button: "Okay",
        });
      });
  };

  return (
    <div className={classNames.container}>
      <FormBase>
        <FormHeader title="Reset" />
        <div className={classNames.inputs_container}>
          <FormInput
            i={{
              id: "password",
              type: "password",
              placeholder: "********",
              label: "Password",
              setFn: setPassword,
            }}
          />
          <FormInput
            i={{
              id: "confirmPassword",
              type: "password",
              placeholder: "********",
              label: "Confirm Password",
              setFn: setConfirmPassword,
            }}
          />
          {loading ? (
            <FormLoader />
          ) : (
            <FormSubmit text="Reset Password" clickFn={resetPassword} />
          )}
        </div>
      </FormBase>
    </div>
  );
};

export default Reset;
